.product-list {
  flex: 4;
  padding-right: 50px;
}

.product-header {
  text-align: center;
  padding: 20px;
}

.product-list-edit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb077;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}

.product-list-delete {
  color: red;
  cursor: pointer;
}

.product-list-img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
