.sidebar {
    flex: 1;
    height: calc(100vh-50px);
    background-color: rgb(255, 255, 255);
    position: sticky;
    top: 50px;
}

.sidebar-wrapper {
    padding: 20px;
    color: #555;
}

.sidebar-menu {
    margin-bottom: 10px;

}

.sidebar-title {
    font-size: 13px;
    color: rgb(187, 186, 186);
}

.sidebar-list {
    list-style: none;
    padding: 5px;
}

.sidebar-list-item {
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.sidebar-list-item.active, .sidebar-list-item:hover {
    background-color: rgb(239, 239, 250);
    border-radius: 10px;
}

.sidebar-icon {
    margin-right: 5px;
    font-size: 20px !important;

}
